import React from "react";



const AddSkill = () =>{


    return(
     <div className="w-3/5 px-3 relative group">
     <input  className="block rounded px-2.5 pb-2.5 pt-5 w-full text-md text-gray-900 bg-gray-100 dark:bg-[#1e293b]/80  border-gray-900/20 dark:border-gray-100/20  appearance-none dark:text-white dark:border-gray-600 dark:focus:border-blue-500 focus:outline-none focus:ring-0 focus:border-blue-600 peer"
      type="text" 
      name="fullName"
      id="fullname"  
      required
      />
      <label   className="absolute text-lg text-gray-500 dark:text-gray-400 duration-300 pl-3 transform -translate-y-4 scale-75 top-4 z-10 origin-[0] left-2.5 peer-focus:text-blue-600 peer-focus:dark:text-blue-500 peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-4"
       htmlFor="fullname">
      Add Skill 
      </label>
        </div>
    )
}
export default AddSkill;